<template>
  <div class="home">
    <Banner :movie="featuredMovie" />

    <MovieRow title="인기 영화" :fetchUrl="popularMoviesUrl" />
    <MovieRow title="최신 영화" :fetchUrl="newReleasesUrl" />
    <MovieRow title="액션 영화" :fetchUrl="actionMoviesUrl" />
  </div>
</template>

<script >
import { ref, onMounted, onBeforeUnmount } from 'vue';
import { faSearch, faUser } from '@fortawesome/free-solid-svg-icons';
import {
  getURL4PopularMovies,
  getURL4ReleaseMovies,
  getURL4GenreMovies,
  fetchFeaturedMovie} from '@/util/movie/URL';
import Banner from '@/views/home-main/Banner.vue';
import MovieRow from '@/views/home-main/MovieRow.vue';

export default {
  name: 'HomeMain',
  components: {
    Banner,
    MovieRow
  },
  setup() {
    const faSearchIcon = faSearch; // Replace with actual icon
    const faUserIcon = faUser; // Replace with actual icon
    const apiKey = localStorage.getItem('TMDb-Key') || '';
    const featuredMovie = ref(null);
    const popularMoviesUrl = ref('');
    const newReleasesUrl = ref('');
    const actionMoviesUrl = ref('');

    popularMoviesUrl.value = getURL4PopularMovies(apiKey);
    newReleasesUrl.value = getURL4ReleaseMovies(apiKey);
    actionMoviesUrl.value = getURL4GenreMovies(apiKey, '28');

    const loadFeaturedMovie = async () => {
      try {
        featuredMovie.value = await fetchFeaturedMovie(apiKey);
      } catch (error) {
        console.error('Error loading featured movie:', error);
      }
    };

    const scrollListener = () => {
      const header = document.querySelector('.app-header');
      if (window.scrollY > 50) {
        header?.classList.add('scrolled');
      } else {
        header?.classList.remove('scrolled');
      }
    };

    onMounted(() => {
      loadFeaturedMovie();
      window.addEventListener('scroll', scrollListener);
      setInterval(() => {
      }, 1000);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('scroll', scrollListener);
    });

    return {
      faSearchIcon,
      faUserIcon,
      featuredMovie,
      popularMoviesUrl,
      newReleasesUrl,
      actionMoviesUrl
    };
  }
};
</script>


<style scoped>
:host {
  display: block;
}

html, body {
  overflow-y: scroll !important;
}

body {
  background-color: #141414 !important;
}
</style>
