import { createRouter, createWebHashHistory } from 'vue-router';
import HomeComponent from '@/components/home/Home.vue';
import SignInComponent from '@/components/sign-in/SignIn.vue';
import HomeMainComponent from '@/components/home/main/Homemain.vue';
import HomeWishlistComponent from '@/components/home/wishlist/HomeWishlist.vue';
import HomeSearchComponent from '@/components/search/HomeSearch.vue';
import HomePopularComponent from '@/components/home/popular/HomePopular.vue';

const routes = [
    {
        path: '/',
        component: HomeComponent,
        meta: {requiresAuth: true},
        children: [
            {
                path: '',
                component: HomeMainComponent
            },
            {
                path: 'popular',
                component: HomePopularComponent
            },
            {
                path: 'wishlist',
                component: HomeWishlistComponent
            },
            {
                path: 'search',
                component: HomeSearchComponent
            }
        ]
    },
    {
        path: '/signin',
        name: 'SignIn',
        component: SignInComponent
    }
];

const router = createRouter({
    history: createWebHashHistory(),
    routes,
});

router.beforeEach((to, _from, next) => {
    const isAuthenticated = localStorage.getItem('TMDb-Key') !== null;

    // 1. 인증이 필요한 경우 (requiresAuth)
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!isAuthenticated) {
            // 인증되지 않은 상태 -> SignIn으로 리다이렉트
            next({ name: 'SignIn' });
        } else {
            // 인증된 상태 -> 라우팅 진행
            next();
        }
    } else {
        // 2. 로그인 페이지 접근 제한
        if (to.name === 'SignIn' && isAuthenticated) {
            // 이미 인증된 경우 -> 홈 페이지로 리다이렉트
            next({ name: '/' });
        } else {
            // 기타 경우 -> 라우팅 진행
            next();
        }
    }
});

export default router;