<template>
  <div class="popular-container">
    <div class="view-toggle">
      <button @click="setView('grid')" :class="{ active: currentView === 'grid' }">
        <font-awesome-icon :icon="faThIcon" />
      </button>
      <button @click="setView('list')" :class="{ active: currentView === 'list' }">
        <font-awesome-icon :icon="faBarsIcon" />
      </button>
    </div>

    <movie-grid
        v-show="currentView === 'grid'"
        title="인기 영화"
        :fetch-url="fetchURL"
    />

    <movie-infinite-scroll
        v-show="currentView === 'list'"
        :api-key="apiKey"
        genre-code="28"
        sorting-order="all"
        vote-average="-1"
    />
  </div>
</template>



<script >
import { ref, onMounted } from "vue";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { faTh, faBars } from "@fortawesome/free-solid-svg-icons";
import MovieGrid from "@/views/grid/MovieGrid";
import MovieInfiniteScroll from "@/views/scroll/MovieInfiniteScroll";
import { getURL4PopularMovies } from "@/util/movie/URL";

export default {
  name: "HomePopular",
  components: {
    MovieGrid,
    MovieInfiniteScroll,
    FontAwesomeIcon,
  },
  setup() {
    // 아이콘 설정
    const faThIcon = faTh;
    const faBarsIcon = faBars;

    // 상태 관리
    const currentView = ref("grid");
    const apiKey = ref(localStorage.getItem("TMDb-Key") || "");
    const fetchURL = getURL4PopularMovies(apiKey.value);

    // 뷰 전환 함수
    const setView = (view) => {
      currentView.value = view;
    };

    // 초기 설정
    onMounted(() => {
      document.body.style.overflow = "hidden"; // 스크롤 비활성화
    });

    return {
      faThIcon,
      faBarsIcon,
      currentView,
      apiKey,
      fetchURL,
      setView,
    };
  },
};
</script>


<style scoped>
.popular-container {
  padding: 20px;
}

.view-toggle {
  display: flex;
  justify-content: flex-end;
  margin-top: 50px;
}

.view-toggle button {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 15px;
  margin-left: 10px;
  cursor: pointer;
  border-radius: 4px;
}

.view-toggle button.active {
  background-color: #535bf2;
}

@media (max-width: 768px) {
  .view-toggle {
    margin-top: 80px;
  }
}
</style>
