<template>
  <div class="movie-row">
    <h2>{{ title }}</h2>
    <div
        class="slider-container"
        @wheel.prevent="handleWheel"
        @touchstart="handleTouchStart"
        @touchmove="handleTouchMove"
        @touchend="handleTouchEnd"
        @mousemove="showButtons = true"
        @mouseleave="showButtons = false"
    >
      <button
          class="slider-button left"
          @click="slide('left')"
          :style="{ opacity: showButtons && !atLeftEdge ? 1 : 0 }"
          :disabled="atLeftEdge"
      >
        &lt;
      </button>
      <div class="slider-window" ref="sliderWindow">
        <div
            class="movie-slider"
            ref="slider"
            :style="{ transform: 'translateX(' + -scrollAmount + 'px)' }"
        >
          <div
              v-for="movie in movies"
              :key="movie.id"
              class="movie-card"
              @click="toggleWishlist(movie)"
          >
            <img :src="getImageUrl(movie.poster_path)" :alt="movie.title" />
            <div v-if="isInWishlist(movie.id)" class="wishlist-indicator">👍</div>
          </div>
        </div>
      </div>
      <button
          class="slider-button right"
          @click="slide('right')"
          :style="{ opacity: showButtons && !atRightEdge ? 1 : 0 }"
          :disabled="atRightEdge"
      >
        &gt;
      </button>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted, onBeforeUnmount, watch } from "vue";
import { useWishlist } from "@/util/movie/useWishlist";

export default {
  name: "MovieRow",
  props: {
    title: {
      type: String,
      required: true,
    },
    fetchUrl: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const movies = ref([]);
    const scrollAmount = ref(0);
    const showButtons = ref(false);
    const touchStartX = ref(0);
    const touchEndX = ref(0);
    const maxScroll = ref(0);
    const slider = ref(null);
    const sliderWindow = ref(null);

    const { toggleWishlist, isInWishlist } = useWishlist();

    const atLeftEdge = computed(() => scrollAmount.value <= 0);
    const atRightEdge = computed(() => scrollAmount.value >= maxScroll.value);

    const fetchMovies = async () => {
      try {
        const response = await fetch(props.fetchUrl);
        const data = await response.json();
        movies.value = data.results;
        calculateMaxScroll();
      } catch (error) {
        console.error("Error fetching movies:", error);
      }
    };

    const getImageUrl = (path) => `https://image.tmdb.org/t/p/w300${path}`;

    const slide = (direction) => {
      const slideAmount = sliderWindow.value.clientWidth * 0.8;
      if (direction === "left") {
        scrollAmount.value = Math.max(0, scrollAmount.value - slideAmount);
      } else {
        scrollAmount.value = Math.min(maxScroll.value, scrollAmount.value + slideAmount);
      }
    };

    const handleWheel = (event) => {
      event.preventDefault();
      const scrollStep = sliderWindow.value.clientWidth * 0.2;
      if (event.deltaY > 0) {
        scrollAmount.value = Math.min(maxScroll.value, scrollAmount.value + scrollStep);
      } else {
        scrollAmount.value = Math.max(0, scrollAmount.value - scrollStep);
      }
    };

    const handleTouchStart = (event) => {
      touchStartX.value = event.touches[0].clientX;
    };

    const handleTouchMove = (event) => {
      touchEndX.value = event.touches[0].clientX;
    };

    const handleTouchEnd = () => {
      const touchDiff = touchStartX.value - touchEndX.value;
      const minSwipeDistance = 50;
      if (Math.abs(touchDiff) > minSwipeDistance) {
        const direction = touchDiff > 0 ? "right" : "left";
        slide(direction);
      }
    };

    const calculateMaxScroll = () => {
      if (slider.value && sliderWindow.value) {
        maxScroll.value = Math.max(
            0,
            slider.value.scrollWidth - sliderWindow.value.clientWidth
        );
      }
    };

    const handleResize = () => {
      calculateMaxScroll();
      scrollAmount.value = Math.min(scrollAmount.value, maxScroll.value);
    };

    onMounted(() => {
      fetchMovies();
      window.addEventListener("resize", handleResize);
      calculateMaxScroll();
    });

    onBeforeUnmount(() => {
      window.removeEventListener("resize", handleResize);
    });

    watch(movies, () => {
      setTimeout(handleResize, 0);
    });

    return {
      movies,
      scrollAmount,
      showButtons,
      slider,
      sliderWindow,
      atLeftEdge,
      atRightEdge,
      fetchMovies,
      getImageUrl,
      slide,
      handleWheel,
      handleTouchStart,
      handleTouchMove,
      handleTouchEnd,
      toggleWishlist,
      isInWishlist,
    };
  },
};
</script>

<style scoped>
.wishlist-indicator {
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
  padding: 5px;
  border-radius: 50%;
  font-size: 12px;
}

.movie-row {
  margin-bottom: 40px;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.movie-row h2 {
  text-align: left;
  margin-left: 30px;
}

.slider-container {
  position: relative;
  touch-action: pan-y;
}

.slider-window {
  overflow: hidden;
  margin: 0 40px;
}

.movie-slider {
  display: flex;
  transition: transform 0.3s ease;
  padding: 20px 0;
}

.movie-card {
  flex: 0 0 auto;
  width: 200px;
  margin-right: 10px;
  transition: transform 0.3s;
  position: relative;
  cursor: pointer;
}

.movie-card:hover {
  transform: scale(1.05);
}

.movie-card img {
  width: 100%;
  height: auto;
  border-radius: 4px;
}

.slider-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 20px 10px;
  cursor: pointer;
  z-index: 10;
  transition: opacity 0.3s, background-color 0.3s;
}

.slider-button:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.slider-button.left {
  left: 0;
}

.slider-button.right {
  right: 0;
}

.slider-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .movie-row {
    margin-bottom: 20px;
  }

  .movie-card {
    width: 120px;
    margin-right: 5px;
  }

  .movie-slider {
    padding: 0;
  }

  .slider-window {
    margin: 0 10px;
  }
}
</style>
